import './detail.css'
import { useParams, Link } from "react-router-dom";
import {useEffect, useState} from "react";
import {getPokemon, Pokemon} from "../lib/pokemon";
import {Aisho, getAisho} from "../lib/aisho";

export function Detail() {
    let { id } = useParams<{id: string}>()
    const [pokemon, setPokemon] = useState<Pokemon>()
    const [aisho, setAisho] = useState<Aisho>()
    useEffect(() => {
        const pokemon = getPokemon(parseInt(id))
        setPokemon(pokemon)
        setAisho(getAisho(pokemon))
    }, [id])

    return (
        <div>
            <div>
                <Link to="/">top</Link>
            </div>
            { pokemon && aisho &&
                <div>
                    <div id="name">
                        {pokemon.name} {pokemon.pokemonTypes.join("/")}
                    </div>
                    <table id="aisho-table">
                        <tbody>
                            <tr><td>×4</td><td className="right">{aisho.quadruple.join(" / ")}</td></tr>
                            <tr><td>×2</td><td className="right">{aisho.twice.join(" / ")}</td></tr>
                            <tr><td>×1</td><td className="right">{aisho.equal.join(" / ")}</td></tr>
                            <tr><td>×0.5</td><td className="right">{aisho.half.join(" / ")}</td></tr>
                            <tr><td>×0.25</td><td className="right">{aisho.oneQuarter.join(" / ")}</td></tr>
                            <tr><td>×0</td><td className="right">{aisho.zero.join(" / ")}</td></tr>
                        </tbody>
                    </table>
                </div>
            }
        </div>
    )
}
