import {katakanaToHiragana} from "./hiragana";

export enum PokemonType {
    Normal = "ノーマル",
    Fire = "ほのお",
    Water = "みず",
    Grass = "くさ",
    Electric = "でんき",
    Ice = "こおり",
    Fighting = "かくとう",
    Poison = "どく",
    Ground = "じめん",
    Flying = "ひこう",
    Psychic = "エスパー",
    Bug = "むし",
    Rock = "いわ",
    Ghost = "ゴースト",
    Dragon = "ドラゴン",
    Dark = "あく",
    Steel = "はがね",
    Fairy = "フェアリー"
}

export const pokemonTypes: PokemonType[] = [
    PokemonType.Normal,
    PokemonType.Fire,
    PokemonType.Water,
    PokemonType.Grass,
    PokemonType.Electric,
    PokemonType.Ice,
    PokemonType.Fighting,
    PokemonType.Poison,
    PokemonType.Ground,
    PokemonType.Flying,
    PokemonType.Psychic,
    PokemonType.Bug,
    PokemonType.Rock,
    PokemonType.Ghost,
    PokemonType.Dragon,
    PokemonType.Dark,
    PokemonType.Steel,
    PokemonType.Fairy,
]

export interface Pokemon {
    id: number
    name: string
    pokemonTypes: PokemonType[]
}

export function getPokemons(s: string): Pokemon[] {
    if (s === "") {
        return []
    }
    return pokemon.filter(pokemon => {
        return pokemon.name.includes(s) || katakanaToHiragana(pokemon.name).includes(s)
    })
}

export function getPokemon(id: number): Pokemon {
    return pokemon.find(pokemon => pokemon.id === id) || pokemon[0]
}

