function getDefence(pokemonType: PokemonType): Defence {
    console.log(pokemonType)
    return defences.find(defence => {
        console.log(defence.pokemonType === pokemonType as PokemonType)
        return defence.pokemonType === pokemonType as PokemonType
    })!
}

function createDefence(
    pokemonType: PokemonType,
    twice: PokemonType[],
    half: PokemonType[],
    zero: PokemonType[],
): Defence {
    const equal = pokemonTypes
        .filter(pokemonType => !twice.includes(pokemonType))
        .filter(pokemonType => !half.includes(pokemonType))
        .filter(pokemonType => !zero.includes(pokemonType))

    return {
        pokemonType,
        twice,
        equal,
        half,
        zero
    }
}
